/**
 * Tools
 */

 export default class Util{

    private static grayGlProgram = null;
    private static originalGlProgram = null;

    private static TOUCH_LAYOUT_ZORDER = -1000;

    public static fileExist(path) : boolean{
        return false;
    }

    public static saveItem(key : string, data : any){
        let jsonObj = JSON.stringify(data);
        cc.sys.localStorage.setItem(key,jsonObj);
     }

     public static loadItem(key : string){
         return JSON.parse(cc.sys.localStorage.getItem(key));
     }

     public static removeItem(key){
        cc.sys.localStorage.removeItem(key);
     }

     public static sprite(path : string) : cc.Node{
        let node = new cc.Node();
        let sp = node.addComponent(cc.Sprite);
        // sp.sizeMode = cc.Sprite.SizeMode.CUSTOM;
        // Need to set SpriteFrame on sprite to set texture
        let spFrame = new cc.SpriteFrame();
        sp.spriteFrame = spFrame;
        // 可以用cc.SpriteFrame(cc.url.raw(path))
        if (cc.textureCache.getTextureForKey(cc.url.raw(path))){
            sp.spriteFrame.setTexture(cc.textureCache.getTextureForKey(cc.url.raw(path)));
        }else{
            let texture = cc.textureCache.addImage(cc.url.raw(path),null,null);
            sp.spriteFrame.setTexture(texture);
        }
        return node;
     }

     public static sprite9(path : string, left : number, bottom : number, right : number, top : number,
        width ?: number, height ?: number) : cc.Node{
        let node = new cc.Node();
        let sp = node.addComponent(cc.Sprite);
        sp.type = cc.Sprite.Type.SLICED;
        sp.sizeMode = cc.Sprite.SizeMode.CUSTOM;
        let spriteFrame = new cc.SpriteFrame();
        sp.spriteFrame = spriteFrame;
        spriteFrame.insetBottom = bottom;
        spriteFrame.insetLeft = left;
        spriteFrame.insetRight = right;
        spriteFrame.insetTop = top;
        if (cc.textureCache.getTextureForKey(cc.url.raw(path))){
            sp.spriteFrame.setTexture(cc.textureCache.getTextureForKey(cc.url.raw(path)));
        }else{
            cc.textureCache.addImage(cc.url.raw(path),(texture)=>{
                sp.spriteFrame.setTexture(texture);
                if (width){
                    node.width = width;
                }
                if (height){
                    node.height = height;
                }
            },null);
            
        }
        return node;
     }

     private static grayChild(sp){
        
        let vert = `
         attribute vec4 a_position;
         attribute vec2 a_texCoord;
         attribute vec4 a_color;
         varying vec4 v_fragmentColor; 
         varying vec2 v_texCoord; 
         void main() 
         { 
             gl_Position = CC_PMatrix * a_position;
             v_fragmentColor = a_color; 
             v_texCoord = a_texCoord; 
         }
         `
         let frag = `
         #ifdef GL_ES
         precision lowp float;
         #endif
         
         varying vec4 v_fragmentColor;
         varying vec2 v_texCoord;
         void main()
         {
             vec4 c = v_fragmentColor * texture2D(CC_Texture0, v_texCoord);
             gl_FragColor.xyz = vec3(0.2126*c.r + 0.7152*c.g + 0.0722*c.b);
             gl_FragColor.w = c.w;
         }
         `
        if (this.originalGlProgram == null){
            this.originalGlProgram = sp._sgNode.getShaderProgram();
        }
        if (this.grayGlProgram == null){
            this.grayGlProgram = new cc.GLProgram();
            if (cc.sys.isNative){
                this.grayGlProgram.initWithString(vert, frag);
            }else{
                this.grayGlProgram.initWithVertexShaderByteArray(vert, frag);
                this.grayGlProgram.addAttribute(cc.macro.ATTRIBUTE_NAME_POSITION, cc.macro.VERTEX_ATTRIB_POSITION);  
                this.grayGlProgram.addAttribute(cc.macro.ATTRIBUTE_NAME_COLOR, cc.macro.VERTEX_ATTRIB_COLOR);  
                this.grayGlProgram.addAttribute(cc.macro.ATTRIBUTE_NAME_TEX_COORD, cc.macro.VERTEX_ATTRIB_TEX_COORDS);  
            }
            this.grayGlProgram.link();  
            this.grayGlProgram.updateUniforms();
        }
        sp._sgNode.setShaderProgram(this.grayGlProgram);
     }

     public static gray(node : cc.Node){
        let spLst = node.getComponents(cc.Sprite);
        for(let sp of spLst){
            this.grayChild(sp);
        }
        let childLst = node.children;
        for (let node of childLst){
            this.gray(node);
        }
     }

     public static unGray(node : cc.Node){
        let spLst = node.getComponents(cc.Sprite);
        for(let sp of spLst){
            if (this.originalGlProgram == null){
                return ;
            }
            sp._sgNode.setShaderProgram(this.originalGlProgram);
        }
        let childLst = node.children;
        for (let node of childLst){
            this.unGray(node);
        }
     }

     public static label(text : string, fontSize : number,color? : cc.Color,
        width ? : number, lineHeight ? : number, align = cc.Label.HorizontalAlign.LEFT, 
        valign = cc.Label.VerticalAlign.TOP) : cc.Node{
         let node = new cc.Node();
         let label = node.addComponent(cc.Label);
         label.string = text;
         label.fontSize = fontSize;
         label.node.color = color || (new cc.Color(255,255,255,255));
         if (width){
            label.overflow = cc.Label.Overflow.RESIZE_HEIGHT;
            node.width = width;
            label.lineHeight = lineHeight || label.lineHeight;
         }
         label.horizontalAlign = align;
         label.verticalAlign = valign;
         return node;
         // 设置字体方法为
        //  cc.loader.loadRes("FZZYJT",(err,font)=>{
        //     label.font = font;
        // });
     }

     public static labelOutline(text : string, fontSize : number,color? : cc.Color,
        width ? : number, lineHeight ? : number, align = cc.Label.HorizontalAlign.LEFT, 
        valign = cc.Label.VerticalAlign.TOP, outlineColor ?: cc.Color, outlineWidth ?: number) : cc.Node{
        let node = Util.label(text, fontSize, color, width, lineHeight, align, valign);
        let labelOutline = node.addComponent(cc.LabelOutline);
        labelOutline.color = outlineColor || new cc.Color(0,0,0,255);
        labelOutline.width = outlineWidth | 2;
        return node;
    }

    public static number(value : string, filePath : string) : cc.Node{
        let node = Util.label(value,20);
        cc.loader.loadRes(filePath,(err,font)=>{
            if (err){
                console.log("Can't find the artistic font, please confirm path = ");
                return ;
            }
            let label = node.getComponent(cc.Label);
            console.log(font);
            label.fontSize = font.fontSize;
            label.font = font;
        });
        return node;
    }

    public static addTouchLayer(view: cc.Node){
        let node = new cc.Node();
        let layout = node.addComponent(cc.Layout);
        node.opacity = 0;
        node.setContentSize(cc.view.getFrameSize());
        node.on(cc.Node.EventType.TOUCH_START,(event)=>{
            console.log(event);
            return true;
        },view,true);
        view.addChild(node,this.TOUCH_LAYOUT_ZORDER);
    }

    public static tickOnce(thisObj,callback,interval){
        cc.director.getScheduler().scheduleCallbackForTarget(thisObj,callback,interval,0,0);
    }

 }